.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body {
  background-color: #f5f2f0;
}

.title {
  position: absolute;
  text-align: center;
  top:0px;
  width: 100%;
  background-color: #363636;
  color: #fff;
}

.regionName {
  display:inline-block;
  padding:10px;
  margin:10px;
  height:20px;
  width: 150px;
  text-align: left;
}

.physical {
  position: relative;
  width:1000px;
  height:500px;
  overflow: auto;
  /* background-color: #61dafb; */
  top:40px
}

.selectors {
  position: absolute;
  width: 950px;
  top:75px;
}

.titles {
  position: absolute;
  width:950px;
  height:70px;
  /* background-color: #fb61ae; */
}

.region {
  position: relative;
  display:inline-block;
  line-height:20px;
  width: 150px;
  text-align: center;
  left:140px;
  /* background-color: aqua; */
}

.toggle {
  display:inline-block;
  width:150px;
  /* background-color:violet ; */
}

.gene {
  position: absolute;
  width: 850px;
  bottom:30px;
  padding:10px;
  margin:10px;  
  /* background-color: #61dafb; */
}

.geneToggle {
  display:inline-block;
  left:50px;
  /* background-color: bisque; */
}

.reset {
  position: absolute;
  bottom: 0px;
  right:10%;
}

.tilt {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 100%;
  top:10%;
  /* background-color: aquamarine; */
}

.tiltButtons {
  position: absolute;
  top: 90%;
  margin:10px;
  /* left: 50%; */
  /* background-color: coral; */
}

.results {
  position: relative;
  width:100%;
  height: 100%;
  /* background-color: aquamarine; */
}

.diagResults {
  position: absolute;
  /* background-color: coral; */
  margin: 40px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
